<!-- This example requires Tailwind CSS v2.0+ -->
<template>
  <Disclosure as="nav" class="bg-gray-light" v-slot="{ open }">
    <!-- max-w-7xl mx-auto -->
    <div class="h-full l:px-16 l:py-4 2xl:px-20 2xl:py-8 3xl:px-24 3xl:pb-12 3xl:pt-10"> 
      <div class="relative flex items-center justify-between s:h-full xs:h-full l:h-8 2xl:h-10 3xl:h-12">
        <div class="absolute inset-y-0 left-0  flex items-center sm:hidden">
          <!-- Mobile menu button-->
          <DisclosureButton class="inline-flex items-center justify-center p-2 rounded-md text-gray-400 hover:text-white hover:bg-gray-700 focus:outline-none focus:ring-2 focus:ring-inset focus:ring-white">
            <span class="sr-only">Open main menu</span>
            <MenuIcon v-if="!open" class="block h-6 w-6" aria-hidden="true" />
            <XIcon v-else class="block h-6 w-6" aria-hidden="true" />
          </DisclosureButton>
        </div>
        <div class="flex-1 flex items-center justify-center sm:items-stretch sm:justify-between ">
          <div class="flex-shrink-0 flex items-center right-0  sm:h-15 sm:w-24 lg:h-15 lg:w-24 l:h-15 l:w-24 xl:h-15 xl:w-24 2xl:h-20 2xl:w-32 ">
            <img class="block sm:hidden h-8 w-auto" src="../assets/Logo-min-web-black-on-white-70px-width.svg" alt="Workflow" />
            <img class="hidden object-contain sm:block" src="../assets/Logo-Prodigy-V-Cheap-Black-Rev.png" alt="Prodigy" />
          </div>
          <!-- 1.5 rem -->
          <div class="hidden sm:block ">
            <div class="flex">
              <a v-for="item in navigation" :key="item.name" :href="item.href" class="pr-6 sm:pt-3 sm:pt-3 lg:pt-3 l:pt-3 xl:pt-3 2xl:pt-6 items-end font-body text-gray lg:text-2xl  l:text-2xl xl:text-2xl  2xl:text-3xl  hover:text-gray-dark " v-smooth-scroll>{{ item.name }}</a>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- 4rem top bot 6rem right left -->

    <DisclosurePanel class="sm:hidden">
      <div class="px-2 pt-2 pb-3 space-y-1">
        <a v-for="item in navigation" :key="item.name" :href="item.href" :class="[item.current ? 'bg-gray-900 text-white' : 'text-gray-300 hover:bg-black-700 hover:text-white', 'block px-3 py-2 rounded-md text-base font-medium']" :aria-current="item.current ? 'page' : undefined">{{ item.name }}</a>
      </div>
    </DisclosurePanel>
  </Disclosure>
</template>

<script>
import { ref } from 'vue'
import { Disclosure, DisclosureButton, DisclosurePanel } from '@headlessui/vue'
import {  MenuIcon, XIcon } from '@heroicons/vue/outline'

const navigation = [
  { name: 'About Us', href: '#sec-2', current: false },
  { name: 'Services', href: '#sec-3', current: false },
  { name: 'Contact', href: '#sec-4', current: false },
]

export default {
  name: 'Nav',
  components: {
    Disclosure,
    DisclosureButton,
    DisclosurePanel,
    
    MenuIcon,
    XIcon,
  },
  setup() {
    const open = ref(false)

    return {
      navigation,
      open,
    }
  },
}
</script>

<style scoped>
  nav {
    height: 8vh;
  
  }
</style>